@import 'normalize';
@import 'variables';
@import 'animate';
@import 'mixins';
@import 'slick';
//styles
*
{
    box-sizing: border-box;
    transition: all .3s ease-in-out;
    text-decoration: none;
}
html
{
    font: 400 10px $text-ff;
}
p
{
    margin: 0;
}
//headings
h1
{
    font-size: 7.2rem;
    font-weight: 700;
    color: $text-b;
    margin: 0 0 20px 0;
    @media screen and(max-width: 992px)
    {
        font-size: 6.2rem;
    }
    @media screen and(max-width: 576px)
    {
        margin: 10px 0 20px 0;
        font-size: 4.6rem;
    }
}
h2
{
    font-size: 3.6rem;
    font-weight: 700;
    color: $text-c;
    margin: 0 0 0 10px;
    @media screen and(max-width: 992px)
    {
        font-size: 2.6rem;
    } 
    @media screen and(max-width: 768px)
    {
        font-size: 2rem;
    }
}
h3
{
    font-size: 2.4rem;
    font-weight: 900;
    color: $text-c;
    z-index: 1;
    margin-left: 16px;
    @media screen and(max-width: 576px)
    {
        font-size: 2.6rem;  
    }
}
//basic selectors
.container
{
    max-width: 1200px;
    padding: 15px;
    margin: 0 auto;
}
.img_wrap
{
    width: 100%;
    overflow: hidden;
    position: relative;
    padding-top: 100%;
    &--decor
    {
        padding-top: 80%;
        @media screen and(max-width: 768px)
        {
            padding-top: 40%;     
        }
        @media screen and(max-width: 576px)
        {
            padding-top: 60%;     
        }
    }
    &--category
    {
        width: 60%;
        padding-top: 110%;
        margin: 15px 15% 0 15%;
    }
    &--left
    {
        width: 70%;
        margin-left: 0;
    }  
    &--circle
    {
        padding-top: 10%;
    } 
}
//color-figures
.bgc
    { 
        min-height: 100%;
        position: absolute;
        border: $b-width solid transparent;
        &--about
        {
            width: 60%;
            top:50%;
            left:50%;
            transform: translate(-80%, -50%);
            border-radius: 0 110px 0 110px;
            @media screen and(max-width: 992px)
            {
                border-radius: 0 90px 0 90px;
            }
            @media screen and(max-width: 768px)
            {
                border-radius: 0 70px 0 70px;
                transform: translate(-50%, -50%);    
            }
            @media screen and(max-width: 576px)
            {
                width: 100%;
            }
        }
        &--category
        {
            width: 90%;
            top:0%;
            left:0%;
            transform: translate(0%, 0%);
            border-radius: 0 130px 0 130px;
            z-index: -1;
            @media screen and(max-width: 992px)
            {
                border-radius: 0 100px 0 100px;
            }
            @media screen and(max-width: 992px)
            {
                border-radius: 0 80px 0 80px;
            }
        }
        &--image
        {
            width: 100%;
            border-radius: 100px 0 100px 0;     
            padding-top: 125%;
            position: relative;
            box-shadow: 0 0  10px rgba(126, 123, 123, 0.5);
            @media screen and(max-width: 992px)
            {
                border-radius:  55px 0 55px 0;
            }
            @media screen and(max-width: 768px)
            {
                width: 80%;
                border-radius:  55px 0 55px 0;
                padding-top: 100%;
                margin: 0 auto;
            }
        }
        &--comment
        {
            position: inherit;
            min-height: 80%;
            width: 100%;
            border: $b-width solid $blue-c;
            overflow: hidden;
            border-radius: 0 110px 0 110px;
        }
        &--orange
        {
            background-color: $orrange-c;
        }
        &--pink
        {
            background-color: $pink-c;
        }
        &--blue
        {
            background-color: $blue-c;
        }
    }
.img
{
    width: 100%;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    &--right
    {
        width: 70%;
        transform: translate(-45%, -40%);
        @media screen and(max-width: 768px)
        {
            width: 45%;
            transform: translate(-50%, -50%);
        }
        @media screen and(max-width: 576px)
        {
            width: 70%;
        }
    }
    &--spoon
    {
        width: 70%;
    }
    &--category
    {
        width: 80%;
    }
}
.switch--1
{
    @media screen and(max-width: 768px)
    {
        order: 2;   
    }
}
//header
.header
{
    background-image: url(../img/pink_bgc.png);
    background-size: 30% 90%;
    background-position: 100% 0%;
    background-repeat: no-repeat;
    @media screen and(max-width: 768px)
    {
        background-size: 30% 100%;     
    }
    &__info
    {
        margin: 100px auto 0 auto;
        padding-bottom: 100px;
        font-size: 3.6rem;
        font-weight: 400;
        background-image: url(../img/header_bcg.png);
        background-position: 70% 100%;
        background-repeat: no-repeat;
        background-size: auto;
        line-height: 1.2;
        @media screen and(max-width: 992px)
        {
            font-size: 2.4rem;    
        }
        @media screen and(max-width: 768px)
        {
            background-size: contain;       
        }
        @media screen and(max-width: 576px)
        {
            font-size: 1.8rem;    
            margin: 40px auto 0 auto;
            padding-bottom: 40px;
        } 
    }
}
//navbar
.navbar
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: 768px)
    {   
         justify-content: space-around;
    }
    &--footer
    {
        border-top: $b-width solid #c4c4c4;
        padding: 30px 0 25px 0;
    }
    &__logo
    {
        display: flex;
        justify-content: space-around;
        align-items: center;
        span
        {
            text-transform: uppercase;
            color: $text-c;
            padding: 10px;
            font-weight: 700;
            font-size: 2.4rem;
            @media screen and (max-width: 576px)
            {   
                font-size: 1.8rem;
            }
        }
        &--footer
        {
            span
            {
                padding: 0;
            }
        }
        @include hover
        {
            span
            {
                color: $text-h;
                cursor: pointer;
            }
        }
    }
    &__elems
    {
        display: flex;
        list-style-type: none;
        padding: 0;
        margin: 0;
        font-size: 1.6rem;
        font-weight: 600;
        opacity: .75;
        @media screen and (max-width: 992px)
        {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: fixed;
            padding: 15% 0;
            top:0;
            left: 0;
            opacity: 0;
            width: 100%;
            min-height: 100vh;
            background-color: $blue-c;
            font-size: 2.4rem;
            text-align: center;
            transform: translate(0,0) scale(0);
            z-index: 15;
            &.open
            {
               opacity: 1;
               transform: scale(1);
               transition:all .5s ease-in-out;    
            }       
        } 
        &--footer
        {
            width: 100%;
            font-size: 1.6rem;
            color: $text-foottitle;
            display: flex;
            justify-content: space-around;
            align-items: flex-end;
            list-style-type: none;
            padding: 0;
            margin: 0;  
            font-weight: 600;
            opacity: .75;
            @media screen and (max-width: 768px)
            {   
                flex-direction: column;
                align-items: start;
            }
        }
    }
    &__elem
    {
        margin: 0 25px;
        @media screen and (max-width: 992px)
        {   
            margin: 10px 0;
            opacity: 1;
            padding: 15px 10px;   
        }
        &--footer
        {
            margin: 0 20px;
            @media screen and (max-width: 992px)
            {   
                margin: 0 5px;
            }
            @media screen and (max-width: 768px)
            {   
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                align-items: center;
            }   
        }
    }
    &__link
    {
        text-decoration: none;
        color: $nav-c;
        @include hover
        {
            color: $text-h;
            opacity: 1;    
        }
        @media screen and (max-width: 992px)
        {
            border-bottom: 2px solid #fff;
            opacity: 0.75;
            padding: 10px 0;
            color: #fff;
            @include hover
            {
                color: #fff;
                border-color: $pink-c;
                opacity: 1;
            }
        }
        &--footer
        {
            padding: 10px 0;
            @media screen and (max-width: 992px)
            {
                font-size: 1.2rem;
                border-bottom: none;
                opacity: inherit;
                color: inherit;
            }
            @media screen and (max-width: 768px)
            {
                font-size: 1.4rem;
                padding: 0;
            }
            @include hover
            {
                color: $text-h;
                opacity: 1;    
            }
        }
    }
    &__burger
    {
        display: none; 
        margin: 10px; 
        @media screen and (max-width: 992px)
        {
            display: block;
            color: $text-h;
            font-size: 2.3rem;   
            position: fixed;
            top: 0;
            right: 0;
            transform: translate(0, 50%);
            z-index: 16;
        } 
        #cross
        {
            display: none;
        }
        &.open
        {
            #hamburger
                {
                    display: none;
                }
            #cross
            {
                display: block;  
            }
        }
    }
    &__subelems
    {
        padding: 0;
        list-style-type: none;
        margin: 10px 0 0 0;
        @media screen and (max-width: 768px)
        {   
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            margin: 0;
        }
        @media screen and (max-width: 768px)
        {
            width: 100%;
        }
    }
    &__subelem
    {
        font-size: 1.4rem;
        color: $text-foot;
        padding: 8px 0;
        @media screen and (max-width: 992px)
        {
            font-size: 1rem;
        }
        @media screen and (max-width: 768px)
        {
            margin: 0 10px;
            font-size: 1.2rem;
        }
    }
    &__sublink
    {
        text-decoration: none;
        color: inherit;
        @include hover
        {
            color: $nav-c;
        }
    }
}  
#logoPart
{
    width: 100%;
    margin: 10px auto 0 auto;
    @media screen and (max-width: 576px)
    {
        flex-direction: column;
        align-items: start;
    }
}   
//btns    
.btn
{
    padding:15px 25px;
    font-weight: 700;
    font-size: 1.6rem;
    border-width: $b-width;
    border-color: transparent;
    background-color: $text-h;
    color: #fff;
    border-radius: 5px;
    outline: none;
    @media screen and (max-width: 576px)
    {
        padding: 15px;
        margin-top: 10px;
    }
    @include hover
    {
        border-color: $text-c;
        color: $text-c;
        background-color: transparent;
        cursor: pointer;
    }
    &--category
    {
        padding:8px 10px;
        font-weight: 600;
        border-color: $text-c;
        color: $text-c;
        background-color: transparent;
        display: block;
        margin: 0 20% 25px auto;
        @include hover
        {
            border-color: $text-h;
            color: #fff;
            background-color: $text-h;
            cursor: pointer;
        }
    } 
    &--nav 
    {
        text-transform: uppercase;
        padding:10px 35px;
        border-radius: 8px;
        @media screen and (max-width: 992px)
        {
            margin-right: 5%;
        }
        @media screen and (max-width: 576px)
        {
            margin-right: 5%;
            padding:10px 15px;
        }   
    }
    &--center
    {
        display: block;
        margin: 0 auto;
    }
    &--contacts
    {
        border-radius: 0px 5px 5px 0;
        @media screen and (max-width: 576px)
        {
            border-radius: 5px;
        }
    } 
}
//grid
.row
{
    display: flex;
    flex-wrap: wrap;
    &--contact
    {
        padding-top: 40px;
        @media screen and(max-width: 576px)
        {
           padding-top: 20px; 
        }
    }
    &--slider
    {
        align-items: center;
    }
}
.col
{
    margin: 0;
    &--header
    {
        @media screen and(max-width: 768px)
        {
            display: none;
        }
    } 
}
.col-1-2
{
    width: 50%;
    padding: 15px; 
    @media screen and(max-width: 768px)
    {
        width: 100%;
        text-align: center;
    } 
    &--comment
    {
        @media screen and(max-width: 768px)
        {
            width: 50%; 
        } 
        @media screen and(max-width: 576px)
        {
            display: none;    
        } 
    }
}
#photos
{
    @media screen and(max-width: 576px)
    {
        padding: 0;   
    }
}
.col-1-3
{
    width: 33%;
    padding: 15px;
}
.col-2-3
{
    width: 66%;
    padding: 15px;
    &--footer
    {
        @media screen and(max-width: 992px)
        {
            width: 80%;
        }
    }
    @media screen and(max-width: 768px)
    {
        width: 100%;
        text-align: center;
    }
}
.col-1-5
{
    width: 20%;
    padding: 10px;
    @media screen and(max-width: 992px)
    {
        width: 30%;
    }
    @media screen and(max-width: 768px)
    {
        width: 100%;
    }   
}
.col-4-5
{
    width: 80%;
    padding: 10px;
    @media screen and(max-width: 992px)
    {
        width: 70%;
    }
    @media screen and(max-width: 768px)
    {
        width: 100%;
    }
}
//about
#spoon
{
    @media screen and(max-width: 768px)
    {
        display: none;
    }
}
.about
{
    background-image: url(../img/bgc_about.png);
    background-position: 0% 50%;
    background-repeat: no-repeat;
    background-size: auto;
    margin-top: 120px;
    @media screen and(max-width: 992px)
    {
        margin-top: 50px;
    } 
    @media screen and(max-width: 576px)
    {
        margin-top: 20px;
        background-image: none;
    }
    &__block
    {
        font-size: 3.6rem;
        font-weight: 600;
        color: $text-c;
        margin-bottom: 50px;
        align-items: center;
        span
        {
            font-size: 2.4rem;
            color: #000;    
        }
        @media screen and(max-width: 992px)
        {
            font-size: 2.4rem; 
            margin-bottom: 20px;
            span
            {
                font-size: 1.4rem;
                color: #000;    
            }
        }
        @media screen and(max-width: 768px)
        {
            font-size: 2.2rem; 
            margin-bottom: 10px;   
        }
    }
}
.name
{
    display: flex;
    justify-content: start;
    align-items: center;
}
.title
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    svg
    {
        opacity: .5;
    }
    span
    {
        font-size: 7.2rem;
        font-weight: 800;
        color: $nav-c;
        opacity: .1;
        text-transform: uppercase;
        @media screen and(max-width: 992px)
        {
            font-size: 5.8rem;
        }
        @media screen and(max-width: 768px)
        {
            font-size: 4rem;
        }
        @media screen and(max-width: 576px)
        {
            font-size: 3.4rem;
        }
    }
    &__divider
    {
        flex-grow: 1;
         border-bottom: 1px solid black; 
         margin: 5px
    }
    @media screen and(max-width: 576px)
    {
        flex-direction: column;
        align-items: flex-start;
    }
}
//how
.how
{ 
    &__elems
    {
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        margin: 65px auto;
        flex-wrap: wrap;
        padding: 0;
        @media screen and(max-width: 992px)
        {
            margin: 40px auto;
        }
        @media screen and(max-width: 576px)
        {
            margin: 20px auto;
        }
    }
    &__elem
    {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        width: 25%;
        svg
        {
            margin: auto;
        }
        @media screen and(max-width: 992px)
        {
           width: 50%;
        }
        @media screen and(max-width: 576px)
        {
           width: 100%;
        }
    }
    &__number
    {
        font-weight: 600;
        color: $pink-c;
        font-size: 9.6rem;
        opacity: 0.4;
        position: absolute;
        top: 0%;
        left: 40%;
        transform: translate(-150%, 20%);
        z-index: -1;
    }
    &__title
    {
        width: 100%;
        margin: 24px 0;
        text-align: center;
        color: #000;
        font-weight: 700;  
    }
}
//categories
.categories
{
    &__items
    {
        padding: 0;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        list-style-type: none;
    }
    &__item
    {
        width: calc(33% - 2 * 5px);
        margin: 20px 5px;
        position: relative;
        @media screen and(max-width: 992px)
        {
            width: calc(50% - 2 * 5px);    
        }
        @media screen and(max-width: 576px)
        {
            width: calc(100% - 2 * 5px);  
            margin: 5px; 
        }
    }
}
// links for isotope
.links
{
    text-transform: uppercase;
    &__title
    {
        font-size: 3.6rem;
        font-weight: 700;
        color: $text-c;
        opacity: .5;
        margin: 20px 0;
        @media screen and(max-width: 992px)
        {
            font-size: 3.2rem;
        }
        @media screen and(max-width: 768px)
        {
            font-size: 2.8rem;
            text-align: center;
        }
        @media screen and(max-width: 576px)
        {
            font-size: 2.2rem;
            margin: 10px 0;
        }
    }
    &__block
    {
        list-style-type: none;
        padding: 0;
        @media screen and(max-width: 768px)
        {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            text-align: center;
        }   
    }
    &__elem
    {
        @media screen and(max-width: 768px)
        {
            width: calc(33% - 5px * 2);   
        }
    }
    .active
    {
        @include active ($text-h, null, 0 0 10px rgba(126, 123, 123, 0.5), null, null, null);   
    }
    &__link
    {
        color: $text-b;
        font-size: 1.6rem;
        padding: 10px 15px;
        margin-bottom: 5px;
        font-weight: 700;
        @media screen and(max-width: 768px)
        {
            font-size: 1.4rem;   
        }
    }
}
// slider
.slider
{
    position: relative;
    padding: 20px 0 60px 0;
    &__arrow
    {
        position: absolute;
        bottom: 10%;
        right: 50%;
        background-color: transparent;
        border: 0;
        outline: 0;
        color: $text-c;
        font-size: 1.4rem;
        font-weight: 600;
        z-index: 10;  
        &--prev
        {
            transform: translate(100%, 0);
            @media screen and(max-width: 576px)
            {
                transform: translate(-50%, 0);   
            }
        }
        &--next
        {
           
            right: 2%;
            transform: translate(0%, 0);
        }
        @media screen and(max-width: 768px)
        {
            bottom: 6%;
        }
        @media screen and(max-width: 576px)
        {
            bottom: 9%;
        }
    }
    &__dots
    {
        list-style-type: none;
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 10;
        li
        {
            display: inline-block;
            margin: 0 5px;
            button
            {
                width: 40px;
                height: 8px;
                padding: 0;
                outline: 0;
                font-size: 0;
                border: none;
                background-color: $text-comment;    
                border-radius: 8px;
                @include hover
                {
                    background-color: $text-h;
                    cursor: pointer;
                }
            }
            &.slick-active button
            {  
                background-color: $text-h;
            }
        }   
    }
}
.content
{
    padding: 55px 15px;
    font-weight: 600;
    font-size: 1.4rem;
    color: $text-c;
    @media screen and(max-width: 992px)
    {
        padding: 15px;
    }
}
.author
{
    display: flex;
    justify-content: start;
    padding-top: 25px;
    &__img
    {
        position: relative;
        display: inline-block;
        box-shadow: 0 0 10px rgba(126, 123, 123, 0.5);
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        vertical-align: middle;
        margin-right: 5px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        align-items: center;
    }
    &__info
    {
       display: flex;
       flex-direction: column;
       color: #AFAFAF;
       font-size: 1.2rem;
       justify-content: space-around;
       padding: 5px;
    }
    &__name
    {
        display: block;
    }
    &__location
    {
        display: block;
    }
    @media screen and(max-width: 992px)
    {
        justify-content: center;
    }
}
.gl-star-rating.gl-star-rating--ltr
{
    margin-bottom: 15px;
    @media screen and(max-width: 992px)
    {
        .gl-star-rating--stars.s0
        {    
            margin: 0 auto;
        }
    }
}
#map 
{
    height: 50vh;
    width: 100%;
    opacity: .75;
}
.gradient
{
  background: linear-gradient(rgba(255, 255, 255, 0.2) 0%, rgba(165, 165, 165, 0.2) 100%);
}
.contacts
{
    position: relative;
    font-size: 1.6rem;
    line-height: 2.6rem;
    text-align: center;
    padding: 20px 0 0 0;
    b
    {
        font-size: 1.8rem;
    }
    &__bgc
    {
        width: 80%;
        margin: 0 auto;
        background-color: $pink-c;
        @media screen and(max-width: 992px)
        {
            width: 100%;
        }
    }
   &__newsletter
    {
       display: flex;
        justify-content: center;
       flex-direction: column;
    }
    &__subscribe
    {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: 20px 0 0 0;
    }
    &__input
    {
        width: 60%;  
        padding: 10px 15px;
        outline: none;
        border: $b-width solid $text-h;
        color: $info-c;
        font-size: 1.6rem;
        border-radius: 8px 0 0 8px;
        @include hover
        {
            border-color: $text-b;
        }
        @media screen and(max-width: 576px)
        {
            width: 100%;
            border-radius: 8px;
        }
    }
}
.footer
{
    background-image: url(../img/footer_bgc.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: $text-foot;
    padding: 60px 0 0 0;
    &__copyright
    {
        text-decoration: none;
        display: block;
        text-align: center;
        font-size: 1.2rem;
        font-weight: 600;
        margin: 30px 0 25px 0;
        @include hover
        {
            color: $text-h;
        }
    }
    @media screen and(max-width: 992px)
    {
        padding: 0;  
    }
    @media screen and(max-width: 768px)
    {
        background-size: contain;
        background-position: bottom;
    }
}