@mixin hover {
    &:hover,
    &:focus,
    &:active {
        @content;
    }
}

@mixin media($w) {
    @media screen and (max-width: $w){
        @content;
    }
}
@mixin active ($bgc, $c, $bs, $bc, $bw, $bst)
{
    background-color: $bgc;
    color: $c;
    box-shadow: $bs;
    border: $bw $bst $bc;
}